/* Fix for * { box-sizing: border-box; } */
/* http://ansciath.tumblr.com/post/7347495869/css-aspect-ratio */
/* Don't allow text selection */
/* Gradients for hue, saturation and value instead of images.  Not pretty... but it works */
/* IE filters do not support multiple color stops.
   Generate 6 divs, line them up, and do two color gradients for each.
   Yes, really.
 */
/* Clearfix hack */
/* Mobile devices, make hue slider bigger so it is easier to slide */
/*
Theme authors:
Here are the basic themeable display options (colors, fonts, global widths).
See http://bgrins.github.io/spectrum/themes/ for instructions.
*/
/* Input */
/* Palettes */
/* Initial */
/* Buttons */
/* Replacer (the little preview div that shows up instead of the <input>) */
/* Buttons: http://hellohappy.org/css3-buttons/ */
.sp-container {
	position: absolute;
	top: 0;
	inset-inline-start: 0;
	display: inline-block;
	*display: inline;
	*zoom: 1;
	z-index: 9999994;
	overflow: hidden;
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box;
	-webkit-user-select: none;
	-moz-user-select: -moz-none;
	-o-user-select: none;
	user-select: none;
	border-radius: 0;
	background-color: #ECECEC;
	border: solid 1px $border;
	padding: 0;
	font: normal 12px "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Verdana, sans-serif;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	-ms-box-sizing: border-box;
	box-sizing: border-box;
	padding-block-end: 0;
	* {
		-webkit-box-sizing: content-box;
		-moz-box-sizing: content-box;
		box-sizing: content-box;
	}
	button {
		-webkit-user-select: none;
		-moz-user-select: -moz-none;
		-o-user-select: none;
		user-select: none;
		font: normal 12px "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Verdana, sans-serif;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		-ms-box-sizing: border-box;
		box-sizing: border-box;
		background-color: #eeeeee;
		background-image: -webkit-linear-gradient(top, #eeeeee, $border);
		background-image: -moz-linear-gradient(top, #eeeeee, $border);
		background-image: -ms-linear-gradient(top, #eeeeee, $border);
		background-image: -o-linear-gradient(top, #eeeeee, $border);
		background-image: linear-gradient(to bottom, #eeeeee, $border);
		border: 1px solid $border;
		border-block-end: 1px solid $border;
		border-radius: 2px;
		color: #333;
		font-size: 14px;
		line-height: 1;
		padding: 5px 4px;
		text-align: center;
		text-shadow: 0 1px 0 #eee;
		vertical-align: middle;
		&:hover {
			background-color: #dddddd;
			background-image: -webkit-linear-gradient(top, #dddddd, $border);
			background-image: -moz-linear-gradient(top, #dddddd, $border);
			background-image: -ms-linear-gradient(top, #dddddd, $border);
			background-image: -o-linear-gradient(top, #dddddd, $border);
			background-image: linear-gradient(to bottom, #dddddd, $border);
			border: 1px solid $border;
			border-block-end: 1px solid #999;
			cursor: pointer;
			text-shadow: 0 1px 0 #ddd;
		}
		&:active {
			border: 1px solid #aaa;
			border-block-end: 1px solid #888;
			-webkit-box-shadow: inset 0 0 5px 2px #aaaaaa, 0 1px 0 0 #eeeeee;
			-moz-box-shadow: inset 0 0 5px 2px #aaaaaa, 0 1px 0 0 #eeeeee;
			-ms-box-shadow: inset 0 0 5px 2px #aaaaaa, 0 1px 0 0 #eeeeee;
			-o-box-shadow: inset 0 0 5px 2px #aaaaaa, 0 1px 0 0 #eeeeee;
			box-shadow: inset 0 0 5px 2px #aaaaaa, 0 1px 0 0 #eeeeee;
		}
	}
	input {
		font: normal 12px "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Verdana, sans-serif;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		-ms-box-sizing: border-box;
		box-sizing: border-box;
	}
}
.sp-container.sp-flat {
	position: relative;
}
.sp-top {
	position: relative;
	width: 100%;
	display: inline-block;
	margin-block-end: 3px;
}
.sp-top-inner {
	position: absolute;
	top: 0;
	inset-inline-start: 0;
	bottom: 0;
	inset-inline-end: 0;
}
.sp-color {
	position: absolute;
	top: 0;
	inset-inline-start: 0;
	bottom: 0;
	inset-inline-end: 20%;
	font: normal 12px "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Verdana, sans-serif;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	-ms-box-sizing: border-box;
	box-sizing: border-box;
	border: solid 1px $border;
}
.sp-hue {
	position: absolute;
	top: 0;
	inset-inline-end: 0;
	bottom: 0;
	inset-inline-start: 84%;
	height: 100%;
	background: -moz-linear-gradient(top, #ff0000 0%, #ffff00 17%, #00ff00 33%, #00ffff 50%, #0000ff 67%, #ff00ff 83%, #ff0000 100%);
	background: -ms-linear-gradient(top, #ff0000 0%, #ffff00 17%, #00ff00 33%, #00ffff 50%, #0000ff 67%, #ff00ff 83%, #ff0000 100%);
	background: -o-linear-gradient(top, #ff0000 0%, #ffff00 17%, #00ff00 33%, #00ffff 50%, #0000ff 67%, #ff00ff 83%, #ff0000 100%);
	background: -webkit-gradient(linear, left top, left bottom, from(#ff0000), color-stop(0.17, #ffff00), color-stop(0.33, #00ff00), color-stop(0.5, #00ffff), color-stop(0.67, #0000ff), color-stop(0.83, #ff00ff), to(#ff0000));
	background: -webkit-linear-gradient(top, #ff0000 0%, #ffff00 17%, #00ff00 33%, #00ffff 50%, #0000ff 67%, #ff00ff 83%, #ff0000 100%);
	background: linear-gradient(to bottom, #ff0000 0%, #ffff00 17%, #00ff00 33%, #00ffff 50%, #0000ff 67%, #ff00ff 83%, #ff0000 100%);
	font: normal 12px "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Verdana, sans-serif;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	-ms-box-sizing: border-box;
	box-sizing: border-box;
	border: solid 1px $border;
}
.sp-clear-enabled {
	.sp-hue {
		top: 33px;
		height: 77.5%;
	}
	.sp-clear {
		display: block;
		position: absolute;
		top: 0px;
		inset-inline-end: 0;
		bottom: 0;
		inset-inline-start: 84%;
		height: 28px;
	}
}
.sp-fill {
	padding-block-start: 80%;
}
.sp-sat {
	position: absolute;
	top: 0;
	inset-inline-start: 0;
	inset-inline-end: 0;
	bottom: 0;
	background-image: -webkit-gradient(linear, 0 0, 100% 0, from(#FFF), to(rgba(204, 154, 129, 0)));
	background-image: -webkit-linear-gradient(left, #FFF, rgba(204, 154, 129, 0));
	background-image: -moz-linear-gradient(left, #fff, rgba(204, 154, 129, 0));
	background-image: -o-linear-gradient(left, #fff, rgba(204, 154, 129, 0));
	background-image: -ms-linear-gradient(left, #fff, rgba(204, 154, 129, 0));
	background-image: linear-gradient(to right, #fff, rgba(204, 154, 129, 0));
	-ms-filter: "progid:DXImageTransform.Microsoft.gradient(GradientType = 1, startColorstr=#FFFFFFFF, endColorstr=#00CC9A81)";
	filter: progid:DXImageTransform.Microsoft.gradient(GradientType = 1, startColorstr='#FFFFFFFF', endColorstr='#00CC9A81');
}
.sp-val {
	position: absolute;
	top: 0;
	inset-inline-start: 0;
	inset-inline-end: 0;
	bottom: 0;
	background-image: -webkit-gradient(linear, 0 100%, 0 0, from(#000000), to(rgba(204, 154, 129, 0)));
	background-image: -webkit-linear-gradient(bottom, #000000, rgba(204, 154, 129, 0));
	background-image: -moz-linear-gradient(bottom, #000, rgba(204, 154, 129, 0));
	background-image: -o-linear-gradient(bottom, #000, rgba(204, 154, 129, 0));
	background-image: -ms-linear-gradient(bottom, #000, rgba(204, 154, 129, 0));
	background-image: linear-gradient(to top, #000, rgba(204, 154, 129, 0));
	-ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=#00CC9A81, endColorstr=#FF000000)";
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00CC9A81', endColorstr='#FF000000');
}
.sp-alpha-enabled {
	.sp-top {
		margin-block-end: 18px;
	}
	.sp-alpha {
		display: block;
	}
}
.sp-alpha-handle {
	position: absolute;
	top: -4px;
	bottom: -4px;
	width: 6px;
	inset-inline-start: 50%;
	cursor: pointer;
	border: 1px solid black;
	background: white;
	opacity: .8;
	-webkit-user-select: none;
	-moz-user-select: -moz-none;
	-o-user-select: none;
	user-select: none;
}
.sp-alpha {
	display: none;
	position: absolute;
	bottom: -14px;
	inset-inline-end: 0;
	inset-inline-start: 0;
	height: 8px;
	-webkit-user-select: none;
	-moz-user-select: -moz-none;
	-o-user-select: none;
	user-select: none;
	position: relative;
	background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAMCAIAAADZF8uwAAAAGUlEQVQYV2M4gwH+YwCGIasIUwhT25BVBADtzYNYrHvv4gAAAABJRU5ErkJggg==);
}
.sp-alpha-inner {
	border: solid 1px $border;
	display: block;
	position: absolute;
	top: 0;
	inset-inline-start: 0;
	bottom: 0;
	inset-inline-end: 0;
}
.sp-clear {
	display: none;
	-webkit-user-select: none;
	-moz-user-select: -moz-none;
	-o-user-select: none;
	user-select: none;
	font: normal 12px "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Verdana, sans-serif;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	-ms-box-sizing: border-box;
	box-sizing: border-box;
	border: solid 1px $border;
}
.sp-clear.sp-clear-display {
	background-position: center;
}
.sp-replacer {
	-webkit-user-select: none;
	-moz-user-select: -moz-none;
	-o-user-select: none;
	user-select: none;
	margin: 0;
	overflow: hidden;
	cursor: pointer;
	padding: 4px;
	display: inline-block;
	*zoom: 1;
	*display: inline;
	border: solid 1px $border;
	background: #eee;
	color: #333;
	vertical-align: middle;
	&:hover {
		border-color: $border;
		color: #111;
	}
}
.sp-preview {
	-webkit-user-select: none;
	-moz-user-select: -moz-none;
	-o-user-select: none;
	user-select: none;
	position: relative;
	width: 25px;
	height: 20px;
	border: solid 1px #222;
	margin-inline-end: 5px;
	float: $float-left;
	z-index: 0;
	position: relative;
	background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAMCAIAAADZF8uwAAAAGUlEQVQYV2M4gwH+YwCGIasIUwhT25BVBADtzYNYrHvv4gAAAABJRU5ErkJggg==);
}
.sp-dragger {
	-webkit-user-select: none;
	-moz-user-select: -moz-none;
	-o-user-select: none;
	user-select: none;
	border-radius: 5px;
	height: 5px;
	width: 5px;
	border: 1px solid #fff;
	background: #000;
	cursor: pointer;
	position: absolute;
	top: 0;
	inset-inline-start: 0;
}
.sp-slider {
	-webkit-user-select: none;
	-moz-user-select: -moz-none;
	-o-user-select: none;
	user-select: none;
	position: absolute;
	top: 0;
	cursor: pointer;
	height: 3px;
	inset-inline-start: -1px;
	inset-inline-end: -1px;
	border: 1px solid #000;
	background: white;
	opacity: .8;
}
.sp-container.sp-dragging {
	.sp-input {
		-webkit-user-select: none;
		-moz-user-select: -moz-none;
		-o-user-select: none;
		user-select: none;
	}
}
.sp-container.sp-input-disabled {
	.sp-input-container {
		display: none;
	}
}
.sp-container.sp-buttons-disabled {
	.sp-button-container {
		display: none;
	}
}
.sp-container.sp-palette-buttons-disabled {
	.sp-palette-button-container {
		display: none;
	}
}
.sp-palette-only {
	.sp-picker-container {
		display: none;
	}
	.sp-palette-container {
		border: 0;
	}
}
.sp-palette-disabled {
	.sp-palette-container {
		display: none;
	}
}
.sp-initial-disabled {
	.sp-initial {
		display: none;
	}
	.sp-input-container {
		width: 100%;
	}
}
.sp-1 {
	height: 17%;
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff0000', endColorstr='#ffff00');
}
.sp-2 {
	height: 16%;
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffff00', endColorstr='#00ff00');
}
.sp-3 {
	height: 17%;
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ff00', endColorstr='#00ffff');
}
.sp-4 {
	height: 17%;
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffff', endColorstr='#0000ff');
}
.sp-5 {
	height: 16%;
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#0000ff', endColorstr='#ff00ff');
}
.sp-6 {
	height: 17%;
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff00ff', endColorstr='#ff0000');
}
.sp-hidden {
	display: none !important;
}
.sp-cf {
	&:before {
		content: "";
		display: table;
	}
	&:after {
		content: "";
		display: table;
		clear: both;
	}
	*zoom: 1;
}
.sp-input-container {
	float: $float-right;
	width: 100px;
	margin-block-end: 4px;
}
.sp-input {
	font-size: 12px !important;
	border: 1px inset;
	padding: 4px 5px;
	margin: 0;
	width: 100%;
	background: transparent;
	border-radius: 2px;
	color: #222;
	&:focus {
		border: 1px solid orange;
	}
}
.sp-input.sp-validation-error {
	border: 1px solid red;
	background: #fdd;
}
.sp-picker-container {
	float: $float-left;
	position: relative;
	padding: 10px;
	padding-block-end: 300px;
	margin-block-end: -290px;
	width: 172px;
	border-inline-start: solid 1px #fff;
}
.sp-palette-container {
	float: $float-left;
	position: relative;
	padding: 10px;
	padding-block-end: 300px;
	margin-block-end: -290px;
	border-inline-end: solid 1px $border;
}
.sp-palette {
	.sp-thumb-el {
		display: block;
		position: relative;
		float: $float-left;
		width: 24px;
		height: 15px;
		margin: 3px;
		cursor: pointer;
		border: solid 2px transparent;
		width: 16px;
		height: 16px;
		margin: 2px 1px;
		border: solid 1px #d0d0d0;
		&:hover {
			border-color: orange;
		}
	}
	.sp-thumb-el.sp-thumb-active {
		border-color: orange;
	}
	*width: 220px;
	max-width: 220px;
	span {
		&:hover {
			border-color: #000;
		}
	}
	span.sp-thumb-active {
		border-color: #000;
	}
	.sp-thumb-inner {
		background-position: 50% 50%;
		background-repeat: no-repeat;
	}
	.sp-thumb-light.sp-thumb-active {
		.sp-thumb-inner {
			background-image: url(data:image/png;base64,);
		}
	}
	.sp-thumb-dark.sp-thumb-active {
		.sp-thumb-inner {
			background-image: url(data:image/png;base64,);
		}
	}
}
.sp-thumb-el {
	position: relative;
	position: relative;
	background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAMCAIAAADZF8uwAAAAGUlEQVQYV2M4gwH+YwCGIasIUwhT25BVBADtzYNYrHvv4gAAAABJRU5ErkJggg==);
}
.sp-initial {
	float: $float-left;
	border: solid 1px $border;
	span {
		width: 30px;
		height: 25px;
		border: none;
		display: block;
		float: $float-left;
		margin: 0;
	}
	.sp-clear-display {
		background-position: center;
	}
}
.sp-palette-button-container {
	float: $float-right;
}
.sp-button-container {
	float: $float-right;
}
.sp-replacer.sp-active {
	border-color: $border;
	color: #111;
}
.sp-replacer.sp-disabled {
	cursor: default;
	border-color: silver;
	color: silver;
}
.sp-dd {
	padding: 2px 0;
	height: 16px;
	line-height: 16px;
	float: $float-left;
	font-size: 10px;
}
.sp-cancel {
	font-size: 11px;
	color: #d93f3f !important;
	margin: 0;
	padding: 2px;
	margin-inline-end: 5px;
	vertical-align: middle;
	text-decoration: none;
	&:hover {
		color: #d93f3f !important;
		text-decoration: underline;
	}
}
.sp-preview-inner {
	display: block;
	position: absolute;
	top: 0;
	inset-inline-start: 0;
	bottom: 0;
	inset-inline-end: 0;
}
.sp-thumb-inner {
	display: block;
	position: absolute;
	top: 0;
	inset-inline-start: 0;
	bottom: 0;
	inset-inline-end: 0;
}
.sp-clear-display {
	background-repeat: no-repeat;
	background-position: center;
	background-image: url(data:image/gif;base64,);
}
@media (max-device-width: 480px) {
	.sp-color {
		inset-inline-end: 40%;
	}
	.sp-hue {
		inset-inline-start: 63%;
	}
	.sp-fill {
		padding-block-start: 60%;
	}
}
